@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=M+PLUS+1p:wght@300&family=Noto+Sans+JP&display=swap);
* {
    margin: 0;
    padding: 0;
}

html {
  font-size: 10px;
}
h1{
  font-size: 5rem;
}
p {
  font-size: 2.5rem;
}

@media screen and (max-width: 640px) {
    html {
      font-size: 7px;
    }
}
:root {
    --main-light-bg-color:#f4f5f7;
    --main-dark-bg-color: #282c34;
    --main-light-font-color: white;
    --main-light-theme-color: #00e091;
    --main-dark-font-color: #282c34;
    --main-dark-theme-color: #1aac5d;
}

.App {
    text-align: center;
    background-color: #f4f5f7;
    background-color: var(--main-light-bg-color);
    height: 100%;
}

body {
    font-family: "Noto Sans JP", sans-serif;
}

.dark-theme-color {
  color: #1aac5d;
  color: var(--main-dark-theme-color);
}

.light-theme-color {
  color: #00e091;
  color: var(--main-light-theme-color);
}

.dark-bg-color {
  background-color: #282c34;
  background-color: var(--main-dark-bg-color);
}

.p-large {
  font-size: 3.5rem;
}

a:hover {
  text-decoration: none;
}
a {
  position: relative;
  display: inline-block;
  transition: .3s;
}
a::before,
a::after {
  position: absolute;
  content: '';
  width: 0;
  height: 1px;
  background-color: #282c34;
  background-color: var(--main-dark-bg-color);
  transition: .3s;
}
a::before {
  top: 0;
  left: 0;
}
a::after {
  bottom: 0;
  right: 0;
}
a:hover::before,
a:hover::after {
  width: 100%;
}
.Headline {
    --headline-spacing: 0.8rem;
    font-family: "Lato", sans-serif;
}

.headline-body {
    margin: 0;
    display: inline-block;
    font-size: 6rem;
    text-align: center;
    letter-spacing: var(--headline-spacing);
    vertical-align: middle;
}

.headline-fontawesome {
    margin-right: calc(var(--headline-spacing) + 1rem);
    font-size: 4rem;
}

.headline-image {
    width: 0.8em;
    margin: 0;
    margin-right: var(--headline-spacing);
    border-radius: 30%;
    border: var(--main-dark-bg-color) 1px solid;
    vertical-align: middle;
}
.section-body {
    width: 80vw;
    min-width: 60rem;
    margin: auto;
    margin-top: 1rem;
    text-align: left;
    padding: 0 2rem;
}

.section-image {
    height: 25rem;
    display: block;
    margin: auto;
    border-radius: 100%;
    box-shadow: 0.1rem 0.1rem 2rem;
}

.section-not-first {
    margin-top: 10rem;
}
.List {
    margin: 0;
    padding: 0;
}

.list-fontawesome {
    margin-right: 1rem;
}
.SNSLink {
}

.sns-link-text {
    margin: 0;
    padding: 0;
}
.Product {
    width: 100%;
    margin-top: 7rem;
}

.product-description {
    font-size: 2rem;
}

.product-tech {
    font-size: 1.5rem;
    border-bottom: solid 1px var(--main-dark-bg-color);
    padding: 0 1rem;
}

.product-link {
    font-size: 2.2rem;
    padding: 0;
    vertical-align: top;
}

.product-details {
    width: 90%;
    border-left: solid 1rem var(--main-dark-bg-color);
    padding-left: 3rem;
}

.product-view {
    width: 100%;
    bottom: 0;
}

.ProductImage {
    margin: 0;
    width: 100%;
    border: var(--main-dark-bg-color) 1px solid;
}

.ProductVideo {
    margin: 0;
    padding: 0;
    border: var(--main-dark-bg-color) 1px solid;
    position: relative;
    width: 100%;
    border-top: solid 0.5rem var(--main-dark-bg-color);
}

.ProductVideo:before {
    content: "";
    display: block;
    padding-top: 56.25% /*16:9固定*/
}

.youtube-frame {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
}
